
export interface IOperation {
  name: string,
  fn: () => any,
  isFaster: number,
  averageTime: number,
  results: number[],
  class?: string
}

export interface IResultItem {
  operation1: IOperation,
  operation2: IOperation,
  isEqual: number,
  Failes?: number,
}

export interface IResult {
  label: string,
  index: number,
  value: IResultItem
}

export interface IServerResult {
  label: string,
  data: IResult
}

export type ArrayItem = number | string | IUser;

export enum DataType {
  Number,
  String,
  Object
};

export const iterationsCount = 1000000;
export const experiments = 10
export interface IUser {
  name: string;
  age: number;
}

