// generate --------------------------------------------------------
export function genArray<T>(generator: () => T, count: number = 10000): T[] {
  const arr: T[] = [];
  for (let i = 0; i < count; i++) {
    arr.push(generator())
  }
  return arr
}

export function genNumber(min: number = 0, max: number = 100): number {
  let rand = min - 0.5 + Math.random() * (max - min + 1);
  return Math.round(rand);
}

export function genObject(): IUser {
  return {
    name: this.genString(),
    age: this.genNumber()
  }
}

export function genString(len: number = 5): string {
  const charSet = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
  let randomString = '';
  for (let i = 0; i < len; i++) {
    let randomPoz = Math.floor(Math.random() * charSet.length);
    randomString += charSet.substring(randomPoz, randomPoz + 1);
  }
  return randomString;
}

export const runExperiment = async (indexes: number[], arr: ArrayItem[], iterations = iterationsCount, calcFn = calcTime): Promise<IResult[]> => {
  const result = [];
  console.log('data: ', arr);
  for (let i of indexes) {
    const searchEl = arr[i];
    const fn2: IOperation = {
      name: 'findIndex',
      fn: () => arr.findIndex(x => x === searchEl),
      results: [],
      averageTime: 0,
      isFaster: 0
    };
    const fn1: IOperation = {
      name: 'indexOf',
      fn: () => arr.indexOf(searchEl),
      results: [],
      averageTime: 0,
      isFaster: 0
    };
    const value = await compare(fn1, fn2, experiments, iterations, calcFn);
    result.push({
      label: `Index of searching element: ${i}`,
      value,
      index: i
    });
  }
  return Promise.resolve(result)

}

export const calcTime = async (fn: () => any, iterations: number = iterationsCount): Promise<number> => {
  performance.mark('a');
  for (let i = 0; i < iterations; i++) {
    fn();
  };
  performance.mark('b');
  for (let i = 0; i < iterations; i++) { };
  performance.mark('c');
  performance.measure("ab", 'a', 'b');
  performance.measure("bc", 'b', 'c');
  const value = performance.getEntriesByName("ab", "measure")[0]?.duration;
  const cycle = performance.getEntriesByName("bc", "measure")[0]?.duration;

  performance.clearMarks();
  performance.clearMeasures();
  const diff = ((value as number) - (cycle as number)) / iterations;
  await Promise.resolve(1);
  return diff;
}




export const compare = async (operation1: IOperation, operation2: IOperation, count: number = experiments, iterations = iterationsCount, calcFn:Function): Promise<IResultItem> => {
  const result: IResultItem = {
    operation1,
    operation2,
    isEqual: 0,
    Failes: 0
  };

  result.operation1.isFaster = result.operation2.isFaster = 0;

  for (let i = 0; i < count; i++) {
    const res1 = await calcFn.call(null,operation1.fn, iterations);
    await new Promise(resolve => setTimeout(resolve, 1));
    const res2 = await calcFn.call(null,operation2.fn, iterations);
    if (!i) {
      result.operation1.averageTime = res1;
      result.operation2.averageTime = res2;
    } else {
      result.operation1.averageTime = (result.operation1.averageTime + res1) / 2;
      result.operation2.averageTime = (result.operation2.averageTime + res2) / 2;
    }
    result.operation1.class = result.operation2.class = '';
    result.operation1.results.push(res1);
    result.operation2.results.push(res2);

    if (res1 && res2) {
      if (res1 < res2)
        result.operation1.isFaster++
      else if (res1 > res2)
        result.operation2.isFaster++;
      else
        result.isEqual++;

      continue;
    }
    else result.Failes++;

  }
  return result;
}

export const genSampleIndexes = (dataLength: number, maxSamplesLength = 20): number[] => {
  const arr = [];
  if (!dataLength) {
    return arr;
  }

  let length = dataLength;
  if (dataLength > maxSamplesLength) {
    length = maxSamplesLength;
  }

  let offset = Math.trunc(dataLength / length);

  for (let i = 0; i < dataLength; i = i + offset) {
    arr.push(i);
  }

  return arr;
}