import { HttpClient } from '@angular/common/http';
import { ChangeDetectionStrategy, ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs';

import { iterationsCount, DataType, experiments, genSampleIndexes, IResult, IServerResult, IUser, runExperiment } from 'src/app/node/utils';

const jsonPath = 'assets/';

@Component({
  selector: 'app-array-test',
  templateUrl: './array-test.component.html',
  styleUrls: ['./array-test.component.css'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class ArrayTestComponent implements OnInit {
  iterationsCount = iterationsCount;
  serverResult: IServerResult[] = [];
  showServerResult = false;
  arraySize = 100;
  experiments = experiments;
  maxExperimentsCount = 8000;
  numbers: number[] = [];
  strings: string[] = [];
  objects: IUser[] = [];
  dataType: DataType = DataType.Number;
  data: { numbers: number[], strings: string[], objects: Object[] };
  processing = false;
  result: IResult[] = [];
  sampleIndexes = [1, Math.ceil(this.arraySize / 2), this.arraySize - 1];

  constructor(private http: HttpClient, private cdr: ChangeDetectorRef) {
    this.getJSON(`${jsonPath}data.json`).subscribe(data => {
      this.data = data
    });
    this.getJSON(`${jsonPath}result.json`).subscribe(data => {
      this.serverResult = data;
      this.cdr.markForCheck();
    });
  }

  ngOnInit(): void {
    //this.numbers = genArray(genNumber, arraySize);
    //this.strings = genArray(genString, arraySize);
    //this.objects = this.genArray(this.genObject.bind(this));

    //this.runExperiment(this.numbers);
  }

  handleClick(): void {
    if (this.processing) {
      return;
    }

    this.processing = true;
    this.sampleIndexes = genSampleIndexes(this.arraySize);
    console.log('indexes: ', this.sampleIndexes);
    let arr = [];
    this.result = [];
    if (this.dataType === DataType.Number)
      arr = this.data.numbers.slice(0, this.arraySize)
    else if (this.dataType === DataType.String)
      arr = this.data.strings.slice(0, this.arraySize)
    else if (this.dataType === DataType.Object)
      arr = this.data.objects.slice(0, this.arraySize)

    setTimeout(async () => {
      this.result = await runExperiment(this.sampleIndexes, arr, this.iterationsCount);
      this.processing = false;
      this.cdr.markForCheck();
    }, 50);
  }

  onExperimentsChange(e: Event): void {
    const count = +(e.target as HTMLInputElement).value;
    if (count > this.maxExperimentsCount) {
      this.experiments = this.maxExperimentsCount;
      return;
    }
    this.experiments = count;
  }

  onDataTypeChange(type: DataType): void {
    this.dataType = type;
  }

  public getJSON(url: string): Observable<any> {
    return this.http.get(url);
  }
}